import React, { Component } from "react";
import _ from "lodash";
class Badges extends Component {
  state = {
    categorias: [],
    bairros: [],
    preco: {},
    area: {},
    quartos: "",
    vagas: ""
  };
  componentDidMount() {}
  componentDidUpdate(prevProps, prevState) {
    if (this.props.pesquisa !== prevProps.pesquisa) {
      const pesquisaFilter = this.props.pesquisa.filter;
      let categorias = [];
      let bairros = [];
      let preco = {};
      let area = {};
      let quartos = "";
      let vagas = "";
      if (pesquisaFilter.Categoria) {
        categorias = pesquisaFilter.Categoria.filter(cat => {
          return cat !== "Coberturaa";
        });
      }
      if (pesquisaFilter.Bairro) {
        const bairrosPrincipais = this.props.topBairros.map(bairro => {
          return bairro.name;
        });
        bairrosPrincipais.push("Outros");
        let outrosBairros = _.difference(
          pesquisaFilter.Bairro,
          bairrosPrincipais
        );
        const selectedBairros = _.difference(
          pesquisaFilter.Bairro,
          outrosBairros
        );
        bairros = selectedBairros;
      }
      if (pesquisaFilter.AreaPrivativa) {
        area = {};
        if (!isNaN(pesquisaFilter.AreaPrivativa[0])) {
          area.minArea = pesquisaFilter.AreaPrivativa[0];
          area.maxArea = pesquisaFilter.AreaPrivativa[1];
        } else if (pesquisaFilter.AreaPrivativa[0] === ">=") {
          area.minArea = pesquisaFilter.AreaPrivativa[1];
        } else if (pesquisaFilter.AreaPrivativa[0] === "<=") {
          area.maxArea = pesquisaFilter.AreaPrivativa[1];
        }
      }
      if (pesquisaFilter.ValorVenda) {
        preco = {};
        if (!isNaN(pesquisaFilter.ValorVenda[0])) {
          preco.minPreco = pesquisaFilter.ValorVenda[0];
          preco.maxPreco = pesquisaFilter.ValorVenda[1];
        } else if (pesquisaFilter.ValorVenda[0] === ">=") {
          preco.minPreco = pesquisaFilter.ValorVenda[1];
        } else if (pesquisaFilter.ValorVenda[0] === "<=") {
          preco.maxPreco = pesquisaFilter.ValorVenda[1];
        }
      }
      if (pesquisaFilter.ValorLocacao) {
        preco = {};
        if (!isNaN(pesquisaFilter.ValorLocacao[0])) {
          preco.minPreco = pesquisaFilter.ValorLocacao[0];
          preco.maxPreco = pesquisaFilter.ValorLocacao[1];
        } else if (pesquisaFilter.ValorLocacao[0] === ">=") {
          preco.minPreco = pesquisaFilter.ValorLocacao[1];
        } else if (pesquisaFilter.ValorLocacao[0] === "<=") {
          preco.maxPreco = pesquisaFilter.ValorLocacao[1];
        }
      }
      if (pesquisaFilter.Dormitorios) {
        quartos = pesquisaFilter.Dormitorios[1];
      }
      if (pesquisaFilter.Vagas) {
        vagas = pesquisaFilter.Vagas[1];
      }
      this.setState({ categorias, bairros, preco, area, quartos, vagas });
    }
  }
  render() {
    const { categorias, bairros, preco, area, quartos, vagas } = this.state;
    const categoriaBadge = categorias.map(cat => {
      return <li>{cat}</li>;
    });
    const bairrosBadge = bairros.map(bairro => {
      return <li>{bairro}</li>;
    });
    const quartosBadge = <li>{quartos}+ Quartos</li>;
    const vagasBadge = <li>{vagas}+ Vagas</li>;
    return (
      <ul className="badges">
        {categoriaBadge}
        {bairrosBadge}
        {quartos !== "" ? quartosBadge : ""}
        {vagas !== "" ? vagasBadge : ""}
      </ul>
    );
  }
}

export default Badges;
