import React, { Component } from "react";
import { strings } from "../../initialState";
import { topBairros } from "../../bairros";
import StickyBox from "react-sticky-box";
import CurrencyInput from "react-currency-input";
import "react-input-range/lib/css/index.css";

// Font Awesome
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { fab } from "@fortawesome/free-brands-svg-icons";
library.add(fab, fas);

const WAIT_INTERVAL = 500;

class BuscaFormulario extends Component {
  state = {
    finalidade: "Comprar",
    showBairros: false,
    bairros: [],
    area: { minArea: null, maxArea: null },
    preco: { minPreco: null, maxPreco: null },
    formItems: {
      bairros: [],
      tipos: [
        {
          name: "Casa",
          value: false
        },
        {
          name: "Apartamento",
          value: false
        },
        {
          name: "Cobertura",
          value: false
        },
        {
          name: "Comercial",
          value: false
        }
      ],
      quartos: [
        {
          name: "1",
          value: false
        },
        {
          name: "2",
          value: false
        },
        {
          name: "3",
          value: false
        },
        {
          name: "4",
          value: false
        },
        {
          name: "5",
          value: false
        }
      ],
      vagas: [
        {
          name: "1",
          value: false
        },
        {
          name: "2",
          value: false
        },
        {
          name: "3",
          value: false
        },
        {
          name: "4",
          value: false
        },
        {
          name: "5",
          value: false
        }
      ]
    }
  };
  componentDidMount() {
    let finalidade = "";
    if (this.props.finalidade === "venda") {
      finalidade = "Comprar";
    } else if (this.props.finalidade === "locacao") {
      finalidade = "Alugar";
    }
    const formItems = { ...this.state.formItems };
    let bairros = topBairros.map(bairro => {
      bairro.value = false;
      return bairro;
    });
    bairros.push({ name: "Outros", value: false });
    formItems.bairros = bairros;
    this.setState({ formItems, finalidade });
  }
  componentDidUpdate(prevProps, prevState) {
    if (this.props.finalidade !== prevProps.finalidade) {
      const preco = { minPreco: null, maxPreco: null };
      if (this.props.finalidade === "venda") {
        this.setState({ finalidade: "Comprar", preco });
      } else if (this.props.finalidade === "locacao") {
        this.setState({ finalidade: "Alugar", preco });
      }
    }
    if (this.props.pesquisa !== prevProps.pesquisa) {
      const formItems = { ...this.state.formItems };
      const preco = { ...this.state.preco };
      const area = { ...this.state.area };
      const filter = { ...this.props.pesquisa.filter };
      if (filter.Categoria) {
        if (filter.Categoria.indexOf("Casa") >= 0) {
          formItems.tipos[0].value = true;
        }
        if (filter.Categoria.indexOf("Apartamento") >= 0) {
          formItems.tipos[1].value = true;
        }
        if (filter.Categoria.indexOf("Coberturaa") >= 0) {
          formItems.tipos[2].value = true;
        }
        if (filter.Categoria.indexOf("Ponto Comercial") >= 0) {
          formItems.tipos[3].value = true;
        }
      }
      if (filter.Bairro) {
        filter.Bairro.map(selectedBairro => {
          formItems.bairros.map(bairro => {
            if (selectedBairro === bairro.name) {
              const indexOf = formItems.bairros.indexOf(bairro);
              formItems.bairros[indexOf].value = true;
            }
            return null;
          });
        });
      }
      if (filter.Vagas) {
        const selectedVaga = this.state.formItems.vagas.filter(vaga => {
          return vaga.name === filter.Vagas[1];
        });
        const indexOf = formItems.vagas.indexOf(selectedVaga[0]);
        formItems.vagas[indexOf].value = true;
      }
      if (filter.Dormitorios) {
        const selectedQuarto = this.state.formItems.quartos.filter(quarto => {
          return quarto.name === filter.Dormitorios[1];
        });
        const indexOf = formItems.quartos.indexOf(selectedQuarto[0]);
        formItems.quartos[indexOf].value = true;
      }
      if (filter.AreaPrivativa) {
        if (!isNaN(filter.AreaPrivativa[0])) {
          area.minArea = filter.AreaPrivativa[0];
          area.maxArea = filter.AreaPrivativa[1];
        } else if (filter.AreaPrivativa[0] === ">=") {
          area.minArea = filter.AreaPrivativa[1];
        } else if (filter.AreaPrivativa[0] === "<=") {
          area.maxArea = filter.AreaPrivativa[1];
        }
      }
      if (filter.ValorVenda) {
        if (!isNaN(filter.ValorVenda[0])) {
          preco.minPreco = filter.ValorVenda[0];
          preco.maxPreco = filter.ValorVenda[1];
        } else if (filter.ValorVenda[0] === ">=") {
          preco.minPreco = filter.ValorVenda[1];
        } else if (filter.ValorVenda[0] === "<=") {
          preco.maxPreco = filter.ValorVenda[1];
        }
      }
      if (filter.ValorLocacao) {
        if (!isNaN(filter.ValorLocacao[0])) {
          preco.minPreco = filter.ValorLocacao[0];
          preco.maxPreco = filter.ValorLocacao[1];
        } else if (filter.ValorLocacao[0] === ">=") {
          preco.minPreco = filter.ValorLocacao[1];
        } else if (filter.ValorLocacao[0] === "<=") {
          preco.maxPreco = filter.ValorLocacao[1];
        }
      }
      this.setState({ formItems, area, preco });
    }
  }
  _handleShowBairros = () => {
    const showBairros = !this.state.showBairros;
    this.setState({ showBairros });
  };
  _handleChangeFinalidade = finalidade => {
    this.setState({ finalidade: finalidade }, () => {
      this.props.onChangeFinalidade(finalidade);
    });
  };
  _handleChangeTipo = item => {
    let tipo = item;
    const formItems = { ...this.state.formItems };
    const tipos = [...this.state.formItems.tipos];
    const indexOf = tipos.indexOf(item);
    tipo.value = !tipo.value;
    tipos[indexOf] = tipo;
    formItems.tipos = tipos;
    const selectedTipos = tipos.filter(tipo => {
      return tipo.value;
    });
    this.setState({ formItems });
    this.props.onChangeTipo(selectedTipos);
  };
  _handleChangeBairro = item => {
    const bairro = item;
    const formItems = { ...this.state.formItems };
    const bairros = [...this.state.formItems.bairros];
    const indexOf = bairros.indexOf(item);
    bairro.value = !bairro.value;
    bairros[indexOf] = bairro;
    formItems.bairros = bairros;
    const selectedBairros = bairros.filter(bairro => {
      return bairro.value;
    });
    this.setState({ formItems });
    this.props.onChangeBairro(selectedBairros);
  };
  _handleChangeArea = event => {
    clearTimeout(this.timer);

    const triggerChangeArea = () => {
      const area = { ...this.state.area };
      const name = event.target.name;
      let value = event.target.value.slice(0, -2);
      if (value <= 0) {
        value = null;
      } else {
        value = parseInt(value);
      }
      area[name] = value;
      this.setState({ area });
      this.props.onChangeArea(area);
    };

    this.timer = setTimeout(triggerChangeArea, WAIT_INTERVAL);
  };
  _handleChangePreco = event => {
    clearTimeout(this.timer);

    const triggerChangePreco = () => {
      const preco = { ...this.state.preco };
      const name = event.target.name;
      let value = event.target.value
        .slice(2)
        .split(".")
        .join("");
      if (value <= 0) {
        value = null;
      } else {
        value = parseInt(value);
      }
      preco[name] = value;
      this.setState({ preco });
      this.props.onChangePreco(preco);
    };

    this.timer = setTimeout(triggerChangePreco, WAIT_INTERVAL);
  };
  _handleChangeQuartos = item => {
    let quarto = item;
    const formItems = { ...this.state.formItems };
    const quartos = [...this.state.formItems.quartos].map(quarto => {
      if (quarto.name !== item.name) {
        quarto.value = false;
      }
      return quarto;
    });
    const indexOf = quartos.indexOf(item);
    quarto.value = !quarto.value;
    quartos[indexOf] = quarto;
    formItems.quartos = quartos;
    const selectedQuartos = quartos.filter(quarto => {
      return quarto.value;
    });
    this.setState({ formItems });
    this.props.onChangeQuartos(selectedQuartos);
  };
  _handleChangeVagas = item => {
    let vaga = item;
    const formItems = { ...this.state.formItems };
    const vagas = [...this.state.formItems.vagas].map(vaga => {
      if (vaga.name !== item.name) {
        vaga.value = false;
      }
      return vaga;
    });
    const indexOf = vagas.indexOf(item);
    vaga.value = !vaga.value;
    vagas[indexOf] = vaga;
    formItems.vagas = vagas;
    const selectedVagas = vagas.filter(vaga => {
      return vaga.value;
    });
    this.setState({ formItems });
    this.props.onChangeVagas(selectedVagas);
  };
  _handleSubmit = event => {
    event.preventDefault();
  };
  _handleLimparFiltros = () => {
    const formItems = {
      tipos: [
        {
          name: "Casa",
          value: false
        },
        {
          name: "Apartamento",
          value: false
        },
        {
          name: "Cobertura",
          value: false
        },
        {
          name: "Comercial",
          value: false
        }
      ],
      quartos: [
        {
          name: "1",
          value: false
        },
        {
          name: "2",
          value: false
        },
        {
          name: "3",
          value: false
        },
        {
          name: "4",
          value: false
        },
        {
          name: "5",
          value: false
        }
      ],
      vagas: [
        {
          name: "1",
          value: false
        },
        {
          name: "2",
          value: false
        },
        {
          name: "3",
          value: false
        },
        {
          name: "4",
          value: false
        },
        {
          name: "5",
          value: false
        }
      ]
    };
    let bairros = topBairros.map(bairro => {
      bairro.value = false;
      return bairro;
    });
    bairros.push({ name: "Outros", value: false });
    formItems.bairros = bairros;
    this.setState({
      bairros: [],
      area: { minArea: null, maxArea: null },
      preco: { minPreco: null, maxPreco: null },
      formItems
    });
    this.props.onLimparFiltros();
  };
  render() {
    const { imovelProp, buscaImoveis } = strings;
    const { lang } = this.props;
    const { formItems } = this.state;
    return (
      <StickyBox offsetTop={15} offsetBottom={15}>
        <form className="buscaForm" onSubmit={this._handleSubmit.bind(this)}>
          <div className="row">
            <div
              className={
                this.state.finalidade === "Comprar"
                  ? "col-6 buscaForm__finalidade active"
                  : "col-6 buscaForm__finalidade"
              }
              onClick={() => this._handleChangeFinalidade("Comprar")}
            >
              <span>{imovelProp[lang].comprar}</span>
            </div>
            <div
              className={
                this.state.finalidade === "Alugar"
                  ? "col-6 buscaForm__finalidade active"
                  : "col-6 buscaForm__finalidade"
              }
              onClick={() => this._handleChangeFinalidade("Alugar")}
            >
              <span>{imovelProp[lang].alugar}</span>
            </div>
          </div>
          <div className="buscaForm__tipo buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "home"]} />
              {imovelProp[lang].tipoDoImovel}
            </h3>
            <div className="buscaForm__box_options">
              {formItems.tipos.map(item => {
                return (
                  <div className="form-check" key={`finalidade${item.name}`}>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      checked={item.value}
                      id={item.name}
                      onChange={() => this._handleChangeTipo(item)}
                    />
                    <span className="checkmark" />
                    <label className="form-check-label" htmlFor={item.name}>
                      {imovelProp[lang][item.name]}
                    </label>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="buscaForm__bairro buscaForm__box">
            <FontAwesomeIcon
              icon={
                this.state.showBairros
                  ? ["fas", "chevron-up"]
                  : ["fas", "chevron-down"]
              }
              onClick={this._handleShowBairros}
            />
            <h3 onClick={this._handleShowBairros}>
              <FontAwesomeIcon icon={["fas", "map-marker-alt"]} />
              {imovelProp[lang].bairro}
            </h3>
            <div
              className={
                this.state.showBairros
                  ? "buscaForm__bairro_box buscaForm__box_options"
                  : "buscaForm__bairro_box buscaForm__box_options hidden"
              }
            >
              {formItems.bairros.map(item => {
                return (
                  <div className="form-check" key={`bairro${item.name}`}>
                    <input
                      className="form-check-input"
                      type="checkbox"
                      value={item.name}
                      checked={item.value}
                      id={item.name}
                      onChange={() => this._handleChangeBairro(item)}
                    />
                    <span className="checkmark" />
                    <label className="form-check-label" htmlFor={item.name}>
                      {item.name}
                    </label>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="buscaForm__area buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "expand"]} />
              {imovelProp[lang].area}
            </h3>
            <span className="minMaxLabel">{imovelProp[lang].areaMinima}</span>

            <CurrencyInput
              className="minArea"
              value={this.state.area.minArea}
              suffix="m²"
              decimalSeparator=""
              thousandSeparator=""
              precision="0"
              name="minArea"
              onChangeEvent={this._handleChangeArea.bind(this)}
            />
            <span className="minMaxConector" />
            <span className="minMaxLabel">{imovelProp[lang].areaMaxima}</span>

            <CurrencyInput
              className="maxArea"
              value={this.state.area.maxArea}
              suffix="m²"
              decimalSeparator=""
              thousandSeparator=""
              precision="0"
              name="maxArea"
              onChangeEvent={this._handleChangeArea.bind(this)}
            />
          </div>
          <div className="buscaForm__preco buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "dollar-sign"]} />
              {imovelProp[lang].preco}
            </h3>
            <span className="minMaxLabel">{imovelProp[lang].precoMinimo}</span>
            <CurrencyInput
              className="minPreco"
              value={this.state.preco.minPreco}
              prefix="R$"
              decimalSeparator=","
              thousandSeparator="."
              precision="0"
              name="minPreco"
              onChangeEvent={this._handleChangePreco.bind(this)}
            />
            <span className="minMaxConector" />
            <span className="minMaxLabel">{imovelProp[lang].precoMaximo}</span>
            <CurrencyInput
              className="maxPreco"
              value={this.state.preco.maxPreco}
              prefix="R$"
              decimalSeparator=","
              thousandSeparator="."
              precision="0"
              name="maxPreco"
              onChangeEvent={this._handleChangePreco.bind(this)}
            />
          </div>
          <div className="buscaForm__quartos buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "bed"]} />
              {imovelProp[lang].quartos}
            </h3>
            <div className="buscaForm__squares">
              {formItems.quartos.map(item => {
                return (
                  <div className="form-check" key={`quarto${item.name}`}>
                    <input
                      className="form-check-input quartosCheckbox"
                      type="checkbox"
                      value={item.name}
                      checked={item.value}
                      id={`quarto${item.name}`}
                      onChange={() => this._handleChangeQuartos(item)}
                    />
                    <label
                      className="form-check-label"
                      htmlFor={`quarto${item.name}`}
                    >
                      {item.name}+
                    </label>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="buscaForm__vagas buscaForm__box">
            <h3>
              <FontAwesomeIcon icon={["fas", "car"]} />
              {imovelProp[lang].vagas}
            </h3>
            <div className="buscaForm__squares">
              {formItems.vagas.map(item => {
                return (
                  <div className="form-check" key={`vaga${item.name}`}>
                    <input
                      className="form-check-input vagasCheckbox"
                      type="checkbox"
                      value={item.name}
                      checked={item.value}
                      id={`vaga${item.name}`}
                      onChange={() => this._handleChangeVagas(item)}
                    />
                    <label
                      className="form-check-label"
                      htmlFor={`vaga${item.name}`}
                    >
                      {item.name}+
                    </label>
                  </div>
                );
              })}
            </div>
          </div>
          <div className="buscaForm__codigo__empty">
            <input
              className="codigoDoImovel"
              placeholder={imovelProp[lang].referencia}
            />
            <span onClick={() => alert("click")}>
              <FontAwesomeIcon icon={["fas", "arrow-right"]} />
            </span>
            <button
              className="btn btn-outline-danger btn-sm"
              onClick={this._handleLimparFiltros}
            >
              <FontAwesomeIcon icon={["fas", "trash-alt"]} />
              {buscaImoveis[lang].limparFiltros}
            </button>
          </div>
        </form>
      </StickyBox>
    );
  }
}

export default BuscaFormulario;
