import React, { Component } from "react";
import _ from "lodash";
import { fetchResultadosBusca, fetchBairros } from "./components/apiCalls";
import ImovelBoxBusca from "./components/busca/imovelBoxBusca";
import PaginationNumbers from "./components/pagination/paginationNumbers";
import BuscaFormulario from "./components/busca/formulario";
import { strings } from "./initialState";
import { topBairros } from "./bairros";
import OrderBy from "./components/busca/orderBy";

// Font Awesome
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fas } from "@fortawesome/free-solid-svg-icons";
import Badges from "./components/busca/badges";
library.add(fas);

class BuscaImoveis extends Component {
  state = {
    loading: false,
    todosBairros: [],
    imoveis: [],
    paginacaoInfo: {},
    orderBy: { order: "", prop: "", show: false },
    pesquisa: {
      fields: [
        "AreaPrivativa",
        "Bairro",
        "Codigo",
        "Categoria",
        "FotoDestaque",
        "FotoDestaquePequena",
        "Dormitorios",
        "ValorLocacao",
        "ValorVenda",
        "Vagas",
        "Status"
      ],
      filter: {
        ExibirNoSite: "Sim",
        Status: ""
      },
      paginacao: { pagina: 1, quantidade: 10 },
      order: {}
    }
  };
  getContent = () => {
    this.setState({ loading: true }, async () => {
      const pesquisa = { ...this.state.pesquisa };
      const query = { ...pesquisa };
      delete query.fields;
      const queryString = encodeURIComponent(JSON.stringify(query));
      this.props.history.push(
        `${this.props.history.location.pathname}?pesquisa=${queryString}`
      );
      const resultados = await fetchResultadosBusca(pesquisa);
      this.setState({
        loading: false,
        imoveis: resultados.imoveis,
        paginacaoInfo: resultados.paginacao
      });
    });
  };
  componentDidMount() {
    const pesquisa = { ...this.state.pesquisa };
    if (this.props.location.search) {
      const queryString = decodeURIComponent(
        this.props.location.search.replace("?pesquisa=", "").toString()
      );
      const queryObj = JSON.parse(queryString);
      for (var propertyName in queryObj) {
        pesquisa[propertyName] = queryObj[propertyName];
      }
    }
    if (this.props.match.params.finalidade === "venda") {
      pesquisa.filter.Status = ["Venda", "Venda e Aluguel"];
    } else if (this.props.match.params.finalidade === "locacao") {
      pesquisa.filter.Status = ["Aluguel", "Venda e Aluguel"];
    }
    this.setState({ pesquisa }, async () => {
      this.getContent();
      const todosBairros = await fetchBairros();
      this.setState({ todosBairros });
    });
  }
  componentDidUpdate(prevProps) {
    if (this.props.location.pathname !== prevProps.location.pathname) {
      const pesquisa = { ...this.state.pesquisa };
      pesquisa.paginacao.pagina = 1;
      delete pesquisa.filter.ValorLocacao;
      delete pesquisa.filter.ValorVenda;
      if (this.props.match.params.finalidade === "venda") {
        pesquisa.filter.Status = ["Venda", "Venda e Aluguel"];
      } else if (this.props.match.params.finalidade === "locacao") {
        pesquisa.filter.Status = ["Aluguel", "Venda e Aluguel"];
      }
      this.setState({ pesquisa }, () => {
        this.getContent();
      });
      // this.getPesquisaFromQuery();
      window.scrollTo(0, 0);
    }
  }
  _handlePageChange = async p => {
    const pesquisa = { ...this.state.pesquisa };
    pesquisa.paginacao.pagina = p;
    this.setState({ pesquisa }, () => {
      this.getContent();
    });
  };
  _handleChangeFinalidade = finalidade => {
    if (finalidade === "Alugar") {
      this.props.history.push(
        `/resultados-de-busca/locacao${this.props.location.search}`
      );
    } else if (finalidade === "Comprar") {
      this.props.history.push(
        `/resultados-de-busca/venda${this.props.location.search}`
      );
    }
  };
  _handleChangeTipo = tipos => {
    const tiposFilter = tipos.map(tipo => {
      return tipo.name;
    });
    const indexComercial = tiposFilter.indexOf("Comercial");
    if (indexComercial >= 0) {
      tiposFilter.splice(indexComercial, 1);
      tiposFilter.push(
        "Casa comercial",
        "Salas/Conjuntos",
        "Prédio Comercial",
        "Ponto Comercial",
        "Loja"
      );
    }
    const indexCasa = tiposFilter.indexOf("Casa");
    if (indexCasa >= 0) {
      tiposFilter.push("Casa em Condomínio");
    }
    const indexCobertura = tiposFilter.indexOf("Cobertura");
    if (indexCobertura >= 0) {
      tiposFilter.push("Coberturaa");
    }
    const indexApartamentos = tiposFilter.indexOf("Apartamento");
    if (indexApartamentos >= 0) {
      tiposFilter.push("Loft", "Kitnet", "Cobertura");
    }
    const pesquisa = { ...this.state.pesquisa };
    pesquisa.paginacao.pagina = 1;
    if (tiposFilter.length > 0) {
      pesquisa.filter.Categoria = tiposFilter;
    } else {
      delete pesquisa.filter.Categoria;
    }
    this.setState({ pesquisa }, () => {
      this.getContent();
    });
  };
  _handleChangeBairro = async selectedBairros => {
    const bairros = selectedBairros.map(bairro => {
      return bairro.name;
    });
    const bairrosPrincipais = topBairros.map(bairro => {
      return bairro.name;
    });
    const outrosBairros = _.difference(
      this.state.todosBairros,
      bairrosPrincipais
    );
    const bairrosFilter = [...bairros];
    const indexOutros = bairros.indexOf("Outros");
    if (indexOutros >= 0) {
      outrosBairros.map(bairro => {
        bairrosFilter.push(bairro);
        return null;
      });
    }
    const pesquisa = { ...this.state.pesquisa };
    pesquisa.paginacao.pagina = 1;
    if (bairrosFilter.length > 0) {
      pesquisa.filter.Bairro = bairrosFilter;
    } else {
      delete pesquisa.filter.Bairro;
    }
    this.setState({ pesquisa }, () => {
      this.getContent();
    });
  };
  _handleChangeArea = area => {
    console.log(area);
    let areaFilter = [];
    const pesquisa = { ...this.state.pesquisa };
    pesquisa.paginacao.pagina = 1;
    if (area.minArea && area.maxArea) {
      areaFilter = [area.minArea, area.maxArea];
    } else if (area.minArea) {
      areaFilter = [">=", area.minArea];
    } else if (area.maxArea) {
      areaFilter = ["<=", area.maxArea];
    }
    pesquisa.filter.AreaPrivativa = areaFilter;
    if (!area.minArea && !area.maxArea) {
      delete pesquisa.filter.AreaPrivativa;
    }
    this.setState({ pesquisa }, () => {
      this.getContent();
    });
  };
  _handleChangePreco = preco => {
    let precoFilter = [];
    if (preco.minPreco && preco.maxPreco) {
      precoFilter = [preco.minPreco, preco.maxPreco];
    } else if (preco.minPreco) {
      precoFilter = [">=", preco.minPreco];
    } else if (preco.maxPreco) {
      precoFilter = ["<=", preco.maxPreco];
    } else {
    }
    let ValorProp = "";
    this.props.match.params.finalidade === "venda"
      ? (ValorProp = "ValorVenda")
      : (ValorProp = "ValorLocacao");
    const pesquisa = { ...this.state.pesquisa };
    pesquisa.paginacao.pagina = 1;
    pesquisa.filter[ValorProp] = precoFilter;
    if (!preco.minPreco && !preco.maxPreco) {
      delete pesquisa.filter[ValorProp];
    }
    this.setState({ pesquisa }, () => {
      this.getContent();
    });
  };
  _handleChangeQuartos = quartos => {
    const pesquisa = { ...this.state.pesquisa };
    pesquisa.paginacao.pagina = 1;
    if (quartos.length > 0) {
      const quarto = quartos[0].name;
      const quartosFilter = [">=", quarto];
      pesquisa.filter.Dormitorios = quartosFilter;
    } else {
      delete pesquisa.filter.Dormitorios;
    }
    this.setState({ pesquisa }, () => {
      this.getContent();
    });
  };
  _handleChangeVagas = vagas => {
    const pesquisa = { ...this.state.pesquisa };
    pesquisa.paginacao.pagina = 1;
    if (vagas.length > 0) {
      const vaga = vagas[0].name;
      const vagasFilter = [">=", vaga];
      pesquisa.filter.Vagas = vagasFilter;
    } else {
      delete pesquisa.filter.Vagas;
    }
    this.setState({ pesquisa }, () => {
      this.getContent();
    });
  };
  _handleLimparFiltros = () => {
    const pesquisa = { ...this.state.pesquisa };
    pesquisa.paginacao.pagina = 1;
    let status = "";

    if (this.props.match.params.finalidade === "venda") {
      status = ["Venda", "Venda e Aluguel"];
    } else if (this.props.match.params.finalidade === "locacao") {
      status = ["Aluguel", "Venda e Aluguel"];
    }
    pesquisa.filter = {
      ExibirNoSite: "Sim",
      Status: status
    };
    this.setState({ pesquisa }, () => {
      this.getContent();
      window.scrollTo(0, 0);
    });
  };
  _handleSelectOrder = item => {
    const finalidade = this.props.match.params.finalidade;
    const orderBy = { ...this.state.orderBy };
    orderBy.show = !orderBy.show;
    if (item.prop === "valor") {
      if (finalidade === "venda") {
        orderBy.prop = "ValorVenda";
      } else {
        orderBy.prop = "ValorLocacao";
      }
      orderBy.order = item.order;
    } else {
      orderBy.prop = item.prop;
      orderBy.order = item.order;
    }
    const pesquisa = { ...this.state.pesquisa };
    pesquisa.paginacao.pagina = 1;
    pesquisa.order = { [orderBy.prop]: orderBy.order };
    this.setState({ orderBy, pesquisa }, () => {
      this.getContent();
    });
  };
  _handleOpenOrderBy = () => {
    const orderBy = { ...this.state.orderBy };
    orderBy.show = !orderBy.show;
    this.setState({ orderBy });
  };
  render() {
    const { imoveis, paginacaoInfo, pesquisa, loading } = this.state;
    const { lang } = this.props;
    const { buscaImoveis } = strings;
    const finalidade = this.props.match.params.finalidade;
    return (
      <div className="buscaImoveis">
        <div className="container">
          <div className="row">
            <div className="col-lg-4">
              <p className="breadcrumbs">
                Anglo Americana{" "}
                <FontAwesomeIcon icon={["fas", "angle-right"]} />{" "}
                {
                  buscaImoveis[lang][
                    `${this.props.match.params.finalidade}Frase`
                  ]
                }
              </p>
            </div>
            <div className="col-lg-8">
              <p className="totalImoveis">
                <strong>
                  {paginacaoInfo.total} {buscaImoveis[lang].imoveisEncontrados}
                </strong>
              </p>
              <OrderBy
                show={this.state.orderBy.show}
                onSelectOrder={this._handleSelectOrder}
                onOpenOrderBy={this._handleOpenOrderBy}
                lang={lang}
                selected={this.state.orderBy}
              />
            </div>
            <div className="col-12">
              <hr className="mt-0" />
            </div>
          </div>
          <div className="row">
            <div className="col-lg-4 form_col">
              <BuscaFormulario
                pesquisa={pesquisa}
                lang={lang}
                finalidade={this.props.match.params.finalidade}
                onChangeFinalidade={this._handleChangeFinalidade}
                onChangeTipo={this._handleChangeTipo}
                onChangeBairro={this._handleChangeBairro}
                onChangeArea={this._handleChangeArea}
                onChangePreco={this._handleChangePreco}
                onChangeQuartos={this._handleChangeQuartos}
                onChangeVagas={this._handleChangeVagas}
                onLimparFiltros={this._handleLimparFiltros}
              />
            </div>
            <div className="col-lg-8">
              {loading ? (
                <div className="loading">
                  <div className="loading_content">Loading</div>
                </div>
              ) : (
                ""
              )}
              {/* <Badges pesquisa={pesquisa} topBairros={topBairros} /> */}
              <div className="row">
                {imoveis.map((imovel, i) => {
                  return (
                    <ImovelBoxBusca
                      imovel={imovel}
                      lang={lang}
                      key={imovel.Codigo}
                      finalidade={finalidade}
                    />
                  );
                })}
              </div>
              <PaginationNumbers
                totalPages={paginacaoInfo.paginas}
                currentPage={pesquisa.paginacao.pagina}
                onPageChange={this._handlePageChange}
                lang={lang}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default BuscaImoveis;
