import React, { Component } from "react";
import { strings } from "../../initialState";

// Font Awesome
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fas } from "@fortawesome/free-solid-svg-icons";
library.add(fas);
class OrderBy extends Component {
  state = {};
  render() {
    const { lang } = this.props;
    const { orderBy } = strings;
    const options = [
      {
        name: `${orderBy[lang].Preço} ${orderBy[lang].asc}`,
        value: { prop: "valor", order: "asc" }
      },
      {
        name: `${orderBy[lang].Preço} ${orderBy[lang].desc}`,
        value: { prop: "valor", order: "desc" }
      },
      {
        name: `${orderBy[lang].Area} ${orderBy[lang].asc}`,
        value: { prop: "AreaPrivativa", order: "asc" }
      },
      {
        name: `${orderBy[lang].Area} ${orderBy[lang].desc}`,
        value: { prop: "AreaPrivativa", order: "desc" }
      },
      {
        name: `${orderBy[lang].Quartos} ${orderBy[lang].asc}`,
        value: { prop: "Dormitorios", order: "asc" }
      },
      {
        name: `${orderBy[lang].Quartos} ${orderBy[lang].desc}`,
        value: { prop: "Dormitorios", order: "desc" }
      },
      {
        name: `${orderBy[lang].Vagas} ${orderBy[lang].asc}`,
        value: { prop: "Vagas", order: "asc" }
      },
      {
        name: `${orderBy[lang].Vagas} ${orderBy[lang].desc}`,
        value: { prop: "Vagas", order: "desc" }
      }
    ];
    return (
      <div className="orderBy">
        <div className="d-inline-block" onClick={this.props.onOpenOrderBy}>
          {orderBy[lang].ordenarPor}
          <FontAwesomeIcon icon={["fas", "caret-down"]} />
        </div>
        <div
          className={
            this.props.show ? "orderBy__options show" : "orderBy__options"
          }
        >
          <ul>
            {options.map(item => {
              return (
                <li
                  key={item.name}
                  onClick={() => this.props.onSelectOrder(item.value)}
                >
                  {item.name}
                </li>
              );
            })}
          </ul>
        </div>
      </div>
    );
  }
}

export default OrderBy;
